@import url(https://fonts.googleapis.com/css2?family=Nanum+Brush+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Monoton&display=swap);
.jumbotron{width:100%;margin:auto}.video-box{position:relative;width:100%;height:95vh;overflow:hidden}.bg-video{position:absolute;width:100%;height:100%;background-position:center;z-index:0;object-fit:cover}.video-box h1{position:absolute;top:65%;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);z-index:1;font-family:'Nanum Brush Script', cursive;font-size:3.5rem;color:#fff}

.message{text-align:center;margin-top:3rem}.message p{margin-bottom:1.5rem;font-size:1rem;font-weight:500;line-height:2rem}

.slider{margin-top:5rem}.title{text-align:center;font-size:1.2rem;font-weight:700;color:#e97879;margin-top:5rem;margin-bottom:3rem}

.carousel .slide{cursor:pointer}.carousel .slide .legend{background:none;top:40%;font-family:'Nanum Brush Script', cursive;font-size:5rem;opacity:1;border:none}

.newitems-box{display:flex;justify-content:space-evenly}.newitems-item{position:relative;display:grid;width:300px}.newitems-item img{width:100%;height:70%}.newitems-item img,.newitems-new{grid-column:1/2;grid-row:1/2}.newitems-new{display:flex;align-items:center;justify-content:center;margin-left:0.5rem;margin-top:0.5rem;font-size:0.8rem;background:#e97879;color:#fff;width:44px;height:44px;border-radius:50%}.newitems-info{margin-top:-4rem;text-align:center}.newitems-name{font-weight:600;margin-bottom:0.5rem}.newitems-price{font-size:0.9rem}

.gallery-grid{display:grid;width:100%;grid-template-columns:repeat(6, 1fr);grid-template-rows:repeat(3, 1fr);grid-column-gap:10px;grid-row-gap:10px}.gallery-grid div img{width:100%;height:100%;background-size:cover}.div1{grid-area:1 / 1 / 3 / 5}.div2{grid-area:1 / 5 / 2 / 7}.div3{grid-area:2 / 5 / 3 / 7}.div4{grid-area:3 / 1 / 4 / 4}.div5{grid-area:3 / 4 / 4 / 7}

.donate{text-align:center}.donate h2{margin-bottom:2rem}.btn{color:#fff;background:#e97879;border-radius:0.5rem;cursor:pointer}.btn:hover{opacity:0.8}.donate-btn{font-size:1.2rem;padding:0.8rem 1.5rem}

.other-links{margin-top:6rem;display:flex;align-items:center}.other-link{width:33.333%;text-align:center}.other-link a:hover{opacity:0.8}

.dropdown{position:absolute;top:3rem;padding:0.2rem 0.8rem;border:1px #e97879 solid;background:#fff;z-index:1}.dropdown li:hover{color:#e97879;cursor:pointer}

a{text-decoration:none;color:inherit}.nav{display:flex;justify-content:space-around;align-items:center;padding:1rem 0}.home{text-decoration:none;font-family:monoton;font-size:2rem;color:#e97879}.home:hover{color:#e9787ad7}.nav-menu{display:flex;font-size:0.95rem;font-weight:500}.nav-menu .nav-btn{position:relative;margin-right:2rem}.nav-item{height:2rem;line-height:2rem}.nav-menu .nav-btn::after{position:absolute;bottom:-6px;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);content:'';width:0;height:3px;background:#e97879;transition:width 0.2s}.nav-menu .nav-btn:hover::after{width:100%}.nav-btn:hover{color:#e97879;cursor:pointer}.nav-right{display:flex}.nav-right div{margin-right:2rem}

.flex{display:flex;justify-content:center}.footer{width:100%;text-align:center;background:#3f3f52;margin-top:8rem}.my-info a:hover{color:#e97879;cursor:pointer}.company-info{background:#f6f6f6;padding:2.5rem 0}.company-info span{color:#3b3b3bc9;font-size:0.9rem}.company-info__main a{margin:1rem;margin-bottom:1.5rem;text-decoration:none}.company-info__main span:hover{color:#000}.company-info__detail-wrap{margin-bottom:1rem}.company-info__detail{margin-bottom:0.6rem}.my-info{padding:1rem 0}.my-info a{color:#fff}.fab{margin:1rem 0.5rem}.my-info h3{font-size:0.8rem;color:#fff}.my-info h3 a{text-decoration:none}

.head-box{position:relative;width:100%;height:50vh;margin-bottom:4rem}.head-title{position:absolute;left:50%;top:50%;-webkit-transform:translateX(-50%) translateY(-50%);transform:translateX(-50%) translateY(-50%);font-family:'Nanum Brush Script', cursive;font-size:3.5rem;color:#fff}

.greeting-box{display:flex}.greeting-img-box{width:45%;height:55vh;margin-right:4rem}.greeting-img{background-image:url(/kaswece/static/media/president.dbd6288c.jpeg);background-position:center center;background-size:contain;background-repeat:no-repeat;width:100%;height:100%}.greeting-msg-box{width:40%}.greeting-msg-box p{line-height:1.5rem}.greeting-msg-box p:first-child{margin-bottom:2rem}.greeting-msg-box p:last-child{font-weight:500;text-align:right}

.centerize{text-align:center;margin-bottom:3rem}.centerize p{line-height:1.5rem}

.guide-box{display:flex;padding:3rem}.guide-left{margin-right:4rem}.guide-left h1{font-size:1.2rem;font-weight:600;color:#e97879;margin-bottom:2rem}.guide-left p{font-size:0.9rem;line-height:1.6rem;margin-bottom:3rem}.guide-right{width:50%;height:50vh;padding:2rem}.guide-img{width:100%;height:100%;background-image:url(/kaswece/static/media/guide-img.e33f85d4.png);background-position:center center;background-size:contain;background-repeat:no-repeat}

.modal-bg{position:fixed;top:0;left:0;bottom:0;right:0;background-color:rgba(0,0,0,0.5);z-index:0}.modal-box{position:fixed;left:50%;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);max-height:80%;width:30rem;height:50%;padding:16px;background:#fff;border-radius:10px;padding:4rem 0 2rem 0;text-align:center}.modal-smallbox{width:100%;height:80%;padding:3rem 0rem;background:#fff;margin-bottom:2rem}.modal-partner__name{color:#e97879;font-size:1.8rem;font-weight:600;margin-bottom:3rem}.modal-partner__info{margin:auto;width:80%;text-align:start}.modal-btn{padding:0.5rem 1rem;color:#e97879;background:#fff;font-size:1.5rem;font-weight:600;border-radius:0.4rem;transition:all 0.2s}.modal-btn:hover{cursor:pointer;color:#fff;background:#e97879}

.partners-box{display:inline-flex;justify-content:center;flex-wrap:wrap;width:100%}.partner-box{width:180px;height:160px;margin:0.4rem;position:relative;display:grid;border-radius:1rem;padding:1.8rem;transition:opacity 0.2s}.partner-name{font-weight:600;line-height:1.5rem;grid-row:2/5}.partner-box:hover{cursor:pointer;opacity:0.7}

.toys-box{display:flex;flex-wrap:wrap;text-align:center;padding-top:3rem}.toy-box{flex:1 1;margin-bottom:3rem}.toy-img{margin:auto;width:370px;height:270px;margin-bottom:1.5rem;overflow:hidden;cursor:pointer}.toy-img img{width:100%;height:100%;transition:0.3s ease-in-out}.toy-img img:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.toy-name{font-size:1.1rem;font-weight:600;margin-bottom:0.8rem}.toy-price{font-size:0.9rem;font-weight:500}

*{margin:0;padding:0;border:0;font-size:100%;font:inherit;box-sizing:border-box}article,aside,details,figcaption,figure,footer,header,menu,nav,section{display:block}body{line-height:1}ol,ul,li{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}button,input,select,textarea{background-color:transparent;outline:none;border:0}.main{width:100%;font-family:'Gothic A1', sans-serif}.container{width:90%;max-width:1540px;margin:0 auto}

